/* Grid System
----------------------------------------------- */

// Set the container width, and override it for fixed navbars in media queries
.container {
    @include container-fixed();

    &--lg {
        @include container-fixed();
    }

    &--no-padding {
        padding-left: 0;
        padding-right: 0;
    }
}

// mobile first defaults
.row {
    @include make-row();
}

// Common styles for small and large grid columns
.col-xs-1,
.col-xs-2,
.col-xs-3,
.col-xs-4,
.col-xs-5,
.col-xs-6,
.col-xs-7,
.col-xs-8,
.col-xs-9,
.col-xs-10,
.col-xs-11,
.col-xs-12,
.col-sm-1,
.col-sm-2,
.col-sm-3,
.col-sm-4,
.col-sm-5,
.col-sm-6,
.col-sm-7,
.col-sm-8,
.col-sm-9,
.col-sm-10,
.col-sm-11,
.col-sm-12,
.col-md-1,
.col-md-2,
.col-md-3,
.col-md-4,
.col-md-5,
.col-md-6,
.col-md-7,
.col-md-8,
.col-md-9,
.col-md-10,
.col-md-11,
.col-md-12,
.col-lg-1,
.col-lg-2,
.col-lg-3,
.col-lg-4,
.col-lg-5,
.col-lg-6,
.col-lg-7,
.col-lg-8,
.col-lg-9,
.col-lg-10,
.col-lg-11,
.col-lg-12 {
    position: relative;
    // Prevent columns from collapsing when empty
    min-height: 1px;
    // Inner gutter via padding
    padding-left: ($grid-gutter-width / 2);
    padding-right: ($grid-gutter-width / 2);
}

/* Extra small grid
----------------------------------------------- */

.col-xs-1,
.col-xs-2,
.col-xs-3,
.col-xs-4,
.col-xs-5,
.col-xs-6,
.col-xs-7,
.col-xs-8,
.col-xs-9,
.col-xs-10,
.col-xs-11 {
    float: left;
}

// col-xs classes
@for $i from 1 through 11 {
    .col-xs-#{$i} {
        width: percentage(($i / $grid-columns));
    }
}

.col-xs-12 {
    width: 100%;
}

// pad-xs classes
@for $i from 0 through 20 {
    $class-num: $i * 15;
    .pad-xs--#{$class-num} {
        padding-top: $class-num + px;
        padding-bottom: $class-num + px;
    }
}

